// DocumentCreationModal.jsx
import React, { useState } from "react";
import CustomModal from "../Modal/customModal";

const DocumentCreationModal = ({ show, onHide, onSave }) => {

    const [type, setType] = useState("Document");

    const [typeDropDownData, setTypeDropDownData] = useState([
        {
            label: "Document",
            val: "Document",
        }
    ])

    const handleCreateDocument = () => {
        onSave(type);
        onHide();
    }

    return (
        <CustomModal
            show={show}
            onHide={onHide}
            title="New Document"
            footerButtons={[
                {
                    label: "Create",
                    variant: "primary",
                    onClick: () => {
                        handleCreateDocument();
                    },
                },
                {
                    label: "Close",
                    variant: "secondary",
                    onClick: onHide,
                },
            ]}
        >
            <div className="row col-12">
                <div className="col-3">
                    <label>Type</label>
                </div>
                <div className="col-9">
                    <select
                        className="form-select mb-2"
                        onChange={(e) => setType(e.target.value)}
                        value={type}
                    >
                        <option value="">-- Select Column --</option>
                        {typeDropDownData.map((item) => (
                            <option key={item?.val} value={item?.val}>
                                {item?.label}
                            </option>
                        ))}
                    </select>
                </div>
            </div>
            <div className="row col-12">
                <div className="col-3">
                    <label>Select File</label>
                </div>
                <div className="col-9">
                    <input type="file" id="formFile" class="form-control"></input>
                </div>
            </div>

        </CustomModal>
    );
};

export default DocumentCreationModal;
